<template>
  <div ref="container" class="images-loader">
    <div
      class="image"
      v-for="image in imgs"
      :key="image.originalUrl"
      :style="{ paddingTop: images.length === 1 ? `${(image.height / image.width) * 100}%` : `50%`}"
      @click="$emit('showSilder', image.originalUrl)"
      >
      <transition name="fade">
        <img v-if="image.hasCompleted" :src="image.url" />
        <template v-else-if="image.hasError">
          <div class="progress">{{ $t("Unable to display") }}</div>
        </template>
        <div v-else class="loader-container">
          <div class="loader"></div>
          <div class="progress">{{ image.progress }}%</div>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
import localForage from 'localforage';
import lib from '@/lib';
import Vue from 'vue';
export default {
  name: 'images-loader',
  props: ['images', 'width'],
  data() {
    return {
      hasError: {},
      hasCompleted: {},
      url: {},
      progress: {}
    };
  },

  mounted() {
    for (const image of this.images) {
      this.getObjectURL(image);
    }
  },
  computed: {
    imgs() {
      return this.images.map(image => {
        return {
          ...image,
          url: this.url[image.originalUrl] || '',
          hasError: this.hasError[image.originalUrl] || false,
          hasCompleted: this.hasCompleted[image.originalUrl] || false,
          progress: this.progress[image.originalUrl] || 0
        };
      });
    },
    imageMaxWidth() {
      return this.isThumbnail
        ? this.$store.state.config.chat.thumbnailSize + 'px'
        : null;
    }
  },
  methods: {
    async preview() {
      if (!this.signedUrl) {
        let filePath = this.url.split('/');
        let fileId = filePath.pop();
        let bucketName = filePath.pop();
        let headers = {};
        this.$store.state.config.downloadFileHeaders.forEach(item => {
          headers[item.name] =
            item.type === 'variable'
              ? this.$store.state.config[item.value]
              : item.value;
        });
        let res = await this.$store.state.api.file.getFileSignedUrl(
          bucketName,
          fileId,
          headers
        );
        this.signedUrl = `https://docs.google.com/viewer?url=${encodeURIComponent(
          res
        )}`;
      }
    },
    async getObjectURL(image) {
      const url = image.thumbnailUrl || image.originalUrl;

      let filePath = url.split('/');
      let fileId = filePath.pop();
      let bucketName = filePath.pop();

      let data = await localForage.getItem(`IMKit-${fileId}`);
      if (data) {
        Vue.set(
          this.url,
          image.originalUrl,
          URL.createObjectURL(lib.upload.b64ToBlob(data))
        );
        Vue.set(this.hasCompleted, image.originalUrl, true);
      } else {
        try {
          let headers = {};
          this.$store.state.config.downloadFileHeaders.forEach(item => {
            headers[item.name] =
              item.type === 'variable'
                ? this.$store.state.config[item.value]
                : item.value;
          });

          let res = await this.$store.state.api.file.download(
            bucketName,
            fileId,
            headers,
            e => {
              if (e.total === 0) {
                Vue.set(this.hasError, image.originalUrl, true);
              }
              Vue.set(
                this.progress,
                image.originalUrl,
                Math.ceil((e.loaded * 100) / e.total)
              );
            }
          );
          let blob = new Blob([res.data], {
            type: res.headers['content-type'].replace(
              'audio/m4a',
              'audio/x-m4a'
            )
          });
          let fr = new FileReader();
          fr.onload = () => {
            let b64 = fr.result;
            localForage.setItem(`IMKit-${fileId}`, b64);
            Vue.set(
              this.url,
              image.originalUrl,
              URL.createObjectURL(lib.upload.b64ToBlob(b64))
            );
            Vue.set(this.hasCompleted, image.originalUrl, true);
          };
          fr.readAsDataURL(blob);
        } catch (e) {
          Vue.set(this.hasError, image.originalUrl, true);
        }
      }
    }
  }
};
</script>
<style lang="scss" scoped>
.images-loader {
  display: flex;
  flex-wrap: wrap;
  border-radius: 10px;
  overflow: hidden;
  gap: 1px;
  width: 100%;
  > .image {
    &:last-child:not(:nth-child(even)) {
      width: 100%;
    }
    width: calc(50% - 0.5px);
    padding-top: 50%;
    overflow: hidden;
    background: #f2f2f2;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    img {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      object-fit: cover;
    }
    .loader-container {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    .progress {
      &:before {
        content: "";
        display: inline-block;
        height: 100%;
        vertical-align: middle;
      }
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      color: #ccc;
      text-align: center;
      font-size: 12px;
      font-weight: 400;
    }
    .loader {
      width: 40px;
      height: 40px;
    }
  }
}
.fade-enter-active {
  transition: opacity 0.5s ease-in-out;
}

.fade-enter-to {
  opacity: 1;
}

.fade-enter {
  opacity: 0;
}
</style>



