import { mapActions } from 'vuex';
import query from 'dom-helpers/query';
import ClickOutside from 'vue-click-outside';
import copy from 'copy-to-clipboard';
import _ from 'lodash';

import Avatar from '@/components/Components/Avatar/index.vue';
import AvatarModal from '@/components/Components/AvatarModal/index.vue';
import Checkbox from '@/components/Components/Checkbox/index.vue';
import FileLoader from '@/components/Components/FileLoader/index.vue';
import ImagesLoader from '@/components/Components/ImagesLoader.vue';
import FileUploader from '@/components/Components/FileUploader/index.vue';
import Location from './Location/index.vue';
import UrlPreview from './UrlPreview/index.vue';
import RequestPayment from './RequestPayment/index.vue';
import Payment from './Payment/index.vue';
import TemplateCarousel from './TemplateCarousel/index.vue';
import Flex from './Flex/index.vue';
import Sticker from './Sticker/index.vue';
import LongPress from 'vue-directive-long-press';
import lib from '@/lib';

const copyTypes = ['text', 'url'];
const replyTypes = ['text', 'url', 'image', 'video', 'sticker', 'location'];
const forwardTypes = ['text', 'url', 'image', 'video', 'audio', 'file'];

export default {
  name: 'RoomChatOutputMessage',
  props: ['message', 'isMobile'],
  data() {
    return {
      clicks: 0,
      clickTimer: 0,
      isShowActionsList: this.isMobile,
      isShowActionsBox: this.isMobile,
      isShowAvatarInfo: false,
      actionsListAlignX: 'right',
      actionsListAlignY: 'top',
      avatarInfo: null,
      contentWidth: 0,
      touchstartX: 0,
      touchstartY: 0,
      contextmenuTimer: null,
      isMoreActionButtonHover: false,
      isMoreActionButtonClicked: false
    };
  },
  components: {
    Avatar,
    AvatarModal,
    Checkbox,
    FileLoader,
    FileUploader,
    Location,
    UrlPreview,
    RequestPayment,
    Payment,
    TemplateCarousel,
    Flex,
    Sticker,
    ImagesLoader
  },
  directives: {
    ClickOutside,
    'long-press': LongPress
  },
  computed: {
    messageClass() {
      let classList = [this.senderType];
      if (this.$store.state.forward.selectMessages) {
        classList.push('forwardMode');
      }
      if (this.message.timespan) {
        classList.push('hasTimespan');
      }
      if (this.message.groupNext) {
        classList.push('groupNext');
      }
      if (this.message.groupPrev) {
        classList.push('groupPrev');
      }
      if (this.message.messageType === 'template') {
        classList.push('template-message');
      }

      return classList;
    },
    senderType() {
      let systemTypes = lib.message.systemTypes;
      if (
        !this.message.sender ||
        ~systemTypes.indexOf(this.message.messageType)
      ) {
        return 'system';
      } else if (this.$store.state.client.client !== this.message.sender.id) {
        return 'others';
      } else {
        return 'self';
      }
    },
    urlExtras() {
      let urls = JSON.parse(this.message.originalUrl);
      let extras = JSON.parse(this.message.extra);
      extras = extras.urls.map((extra, idx) => {
        if (extra.link) {
          return extra;
        } else {
          return {
            ...extra,
            link: urls[idx]
          };
        }
      });
      return extras;
    },
    readedCount() {
      const memberProperties = this.$store.state.roomActive.memberProperties || [];
      const len = memberProperties.filter(
        member => {
          return (
            member.lastRead >= this.message.id &&
            member.client !== this.$store.state.client.client
          );
        }
      ).length;

      if (len === 1 && this.$store.state.roomActive.members.length === 2) {
        return '';
      } else {
        return len;
      }
    },
    onlyEmoji() {
      let onlyEmojiRegex = /^([\uD800-\uDBFF][\uDC00-\uDFFF])+$/;
      return onlyEmojiRegex.test(this.message.message);
    },
    canReply() {
      return (
        !this.message.status &&
        this.$store.state.config.chat.reply &&
        ~replyTypes.indexOf(this.message.messageType)
      );
    },
    canCopy() {
      return (
        !this.message.status && ~copyTypes.indexOf(this.message.messageType)
      );
    },
    actions() {
      let actions = [];

      if (
        !this.message.status &&
        this.$store.state.config.chat.recall &&
        this.senderType === 'self' &&
        this.message.messageType !== 'recall'
      ) {
        actions.push({
          key: 'recall',
          text: this.$i18n.t('Recall')
        });
      }
      if (
        !this.message.status &&
        this.$store.state.config.chat.forward &&
        ~forwardTypes.indexOf(this.message.messageType)
      ) {
        actions.push({
          key: 'forward',
          text: this.$i18n.t('Forward')
        });
      }

      return actions;
    },
    forwardChecked() {
      return !!this.$store.state.forward.messages.find(message => {
        return message.id === this.message.id;
      });
    },
    senderName() {
      if (this.message.sender._id === this.$store.state.client.client) {
        return this.$t('You');
      }
      return (this.message.sender.nickname || this.$t('Unknown Name')) + ' ';
    },
    contentStyle() {
      const { messageType, reply, images } = this.message;
      let style = {};
      if (messageType === 'template') {
        return style;
      }
      if (messageType !== 'recall' && reply && reply.id) {
        style = this.$store.state.config.chat.colors[this.senderType];
      }
      style.width = messageType === 'image' && images && images.length ? '100%' : 'unset';
      style.maxWidth = messageType === 'image' && images && images.length ? '100%' : 'unset';
      return style;
    },
    moreActionButtonIcon() {
      const { isMoreActionButtonClicked, isMoreActionButtonHover } = this
      if (isMoreActionButtonClicked) {
        return  'more-active';
      }
      return isMoreActionButtonHover ? 'more-hover' : 'more';
    }
  },
  mounted() {
    // document.body.addEventListener('touchstart', this.hideActionsList);
    // document.body.addEventListener('mousedown', this.hideActionsList);
    this.contentWidth = this.$refs.message.clientWidth;
    window.addEventListener('resize', () => {
      if (this.$refs.message) {
        this.contentWidth = this.$refs.message.clientWidth;
      }
    });
  },
  beforeDestroy() {
    // document.body.removeEventListener('touchstart', this.hideActionsList);
    // document.body.removeEventListener('mousedown', this.hideActionsList);
  },
  watch: {
    isMoreActionButtonClicked(curr, prev) {
      if (curr && !prev) {
        this.$nextTick(() => {
          this.handleButtonsPosition();
        });
      }
      if (!curr && prev) {
        this.$nextTick(() => {
          this.$refs.actionsBox.style = {};
        })
      }
    }
  },
  methods: {
    addMembersMessage(message) {
      return lib.message.addMembersMessage(message);
    },
    textView(message) {
      return lib.message.textView(message);
    },
    onTouchstart(e) {
      this.onMobileClick(e, e.type === 'touchstart');
      this.contextmenuTimer = setTimeout(() => {
        // this.showActionsList(e);
      }, 500);
    },
    onTouchmove(e) {
      if (this.contextmenuTimer) {
        let clientX = e.touches ? e.touches[0].clientX : e.clientX;
        let clientY = e.touches ? e.touches[0].clientY : e.clientY;
        if (
          Math.abs(clientX - this.touchstartX) > 50 ||
          Math.abs(clientY - this.touchstartY) > 50
        ) {
          clearTimeout(this.contextmenuTimer);
          clearTimeout(this.clickTimer);
          this.contextmenuTimer = null;
          this.clickTimer = null;
          this.clicks = 0;
        }
      }
    },
    onTouchend() {
      let isShowActionsList = this.isShowActionsList;
      if (this.contextmenuTimer) {
        clearTimeout(this.contextmenuTimer);
        this.contextmenuTimer = null;
      }
      this.$nextTick(() => {
        this.isShowActionsList = this.isShowActionsList || isShowActionsList;
      });
    },
    onClick(e, fromTouch = false) {
      // if (this.$store.state.isMobile && !fromTouch) {
      //   return;
      // }
      this.clicks++;
      if (this.clicks === 1) {
        this.clickTimer = setTimeout(() => {
          // 單擊
          this.clicks = 0;
          this.clickTag(e);
          this.showSilder(e);
        }, 300);
      } else {
        // 雙擊
        clearTimeout(this.clickTimer);
        this.clicks = 0;
        this.onDoubleClick();
      }
    },
    onMobileClick(e, fromTouch = false) {
      this.onClick(e, fromTouch);
    },
    onPcClick(e, fromTouch = false) {
      if (this.$store.state.isMobile) {
        return;
      }
      this.onClick(e, fromTouch);
    },
    showAvatar(info) {
      this.avatarInfo = {
        avatar: info.avatarUrl ? decodeURIComponent(info.avatarUrl) : null,
        name: info.nickname
      };
      this.isShowAvatarInfo = true;
    },
    clickTag(e) {
      if (e.target.classList.contains('textTag')) {
        let uid = atob(e.target.dataset.uid);
        let member = this.$store.state.roomActive.members.find(
          m => m._id === uid
        );
        if (member) {
          this.showAvatar({
            avatarUrl: member.avatarUrl,
            nickname: member.nickname
          });
        }
      }
    },
    showSilder(e) {
      switch (this.message.messageType) {
        case 'text':
          if (this.message.originalUrl && query.closest(e.target, '.fileLoaderImage')) {
            this.$emit('showSilder', '');
          }
          break;
        case 'image':
        case 'video':
          if (query.closest(e.target, '.fileLoaderImage')) {
            this.$emit('showSilder', '');
          }
          break;
      }
    },
    gotoMessage(messageId, e) {
      if (e.target.tagName.toLowerCase() === 'a') {
        return;
      }
      this.$emit('gotoMessage', messageId);
    },
    findAncestor(element, className) {
      do {
        if (this.hasClass(element, className)) {
          return true;
        }
        element = element.parentElement;
      } while (element);
      return false;
    },
    hasClass(element, className) {
      if (!element.className) {
        return 0;
      }
      return ~element.className.split(/\s+/g).indexOf(className);
    },
    // hideActionsList() {
    //   this.isShowActionsList = false;
    // },
    setActionsPosition() {
      if (this.isShowActionsList) {
        let actionsBtn = this.$refs.actions;
        while (actionsBtn.length) {
          actionsBtn = actionsBtn[0];
        }
        let pos = this.getPosition(actionsBtn);
        let roomChatWidth = document.querySelector('.roomChatOutput')
          .offsetWidth;
        let roomChatHeight = document.querySelector('.roomChatOutput')
          .offsetHeight;

        // 因為捲軸是自幹的元件，所以要使用這個方法拿到捲動高度
        let scrollTop = parseInt((document.querySelector('.scrollFrontY').style.top).replace('px', ''), 10);
        pos.x += 10;
        pos.y += 10;

        pos.y -= scrollTop;

        if (pos.x > roomChatWidth / 2) {
          this.actionsListAlignX = 'right';
        } else {
          this.actionsListAlignX = 'left';
        }
        if (pos.y > roomChatHeight / 2) {
          this.actionsListAlignY = 'bottom';
        } else {
          this.actionsListAlignY = 'top';
        }
      }
    },
    showActionsList(e) {
      this.eventFire(document.body, 'mousedown');
      this.isShowActionsList = !this.isShowActionsList;
      this.setActionsPosition();
    },
    getPosition(element) {
      let x = 0;
      let y = 0;
      while (element && !element.classList.contains('scrollBar')) {
        x += element.offsetLeft - element.scrollLeft + element.clientLeft;
        y += element.offsetTop - element.scrollTop + element.clientTop;
        element = element.offsetParent;
      }

      return { x: x, y: y };
    },
    doAction(key) {
      switch (key) {
        case 'reply':
          this.$emit('setReply');
          break;
        case 'forward':
          this.$store.state.forward.selectMessages = true;
          this.$store.state.forward.messages = [this.message];
          this.$emit('setForward');
          break;
        case 'recall':
          this.recallMessage({
            roomId: this.$store.state.roomActive._id,
            messageId: this.message._id
          });
          break;
        case 'copy':
          this.copyMessage();
          break;
        default:
          break;
      }
      this.isShowActionsList = false;
    },
    copyMessage() {
      copy(this.message.message);
    },
    clickForwardCheckbox() {
      let idx = this.$store.state.forward.messages.findIndex(message => {
        return message.id === this.message.id;
      });
      if (idx < 0) {
        if (this.$store.state.forward.messages.length < 10) {
          this.$store.state.forward.messages.push(this.message);
        }
      } else {
        this.$store.state.forward.messages.splice(idx, 1);
      }
    },
    sendActionMessage(action) {
      let msg = {
        messageType: 'text',
        message: action.displayText || action.label
      };
      this.sendMessage(msg);
      this.$nextTick(() => {
        this.$emit('scrollToBottom');
      });
    },
    eventFire(el, etype) {
      if (el.fireEvent) {
        el.fireEvent('on' + etype);
      } else {
        var evObj = document.createEvent('Events');
        evObj.initEvent(etype, true, false);
        el.dispatchEvent(evObj);
      }
    },
    getAvatarUrl() {
      const senderId = this.message.sender.id;
      const roomOwnerId = _.get(this.$store.state.config, 'roomOwner.id');
      const roomOwnerAvatarUrl = _.get(this.$store.state.config, 'roomOwner.avatarUrl');
      if (senderId === roomOwnerId && roomOwnerAvatarUrl) {
        return roomOwnerAvatarUrl;
      }
      return this.message.sender.avatarUrl;
    },
    getSenderName() {
      const senderId = this.message.sender.id;
      const roomOwnerId = _.get(this.$store.state.config, 'roomOwner.id');
      const roomOwnerName = _.get(this.$store.state.config, 'roomOwner.name');
      if (senderId === roomOwnerId && roomOwnerName) {
        return roomOwnerName;
      }
      return this.message.sender.nickname;
    },
    getReplyAvatarUrl() {
      const senderId = this.message.reply.sender.id;
      const roomOwnerId = _.get(this.$store.state.config, 'roomOwner.id');
      const roomOwnerAvatarUrl = _.get(this.$store.state.config, 'roomOwner.avatarUrl');
      if (senderId === roomOwnerId && roomOwnerAvatarUrl) {
        return roomOwnerAvatarUrl;
      }

      return this.message.reply.sender.avatarUrl;
    },
    getReplyName() {
      const senderId = this.message.reply.sender.id;
      const roomOwnerId = _.get(this.$store.state.config, 'roomOwner.id');
      const roomOwnerName = _.get(this.$store.state.config, 'roomOwner.name');
      if (senderId === roomOwnerId && roomOwnerName) {
        return roomOwnerName;
      }

      return this.message.reply.sender.nickname;
    },
    onLongPressStart() {
      if (!this.$store.state.isMobile) {
        return;
      }
      this.isShowActionsBox = true;
      this.isShowActionsList = true;
      setTimeout(() => {
        this.setActionsPosition();
      }, 0);
    },
    onDoubleClick() {
      if (this.$store.state.config.chat.reply &&
        ~replyTypes.indexOf(this.message.messageType)
      ) {
        this.$emit('setReply');
      }
    },
    mouseOver() {
      this.isShowActionsBox = true;
      // this.isShowActionsList = false;
    },
    mouseLeave() {
      if (!this.isMobile) {
        this.isShowActionsBox = false;
      }
      // this.isShowActionsList = false;
    },
    openActionButtons() {
      this.isMoreActionButtonClicked = !this.isMoreActionButtonClicked;
    },
    handleClickOutside() {
      if (!this.isMobile) {
        this.isShowActionsBox = false;
        this.isShowActionsList = false;
      }
      this.isMoreActionButtonClicked = false;
    },
    handleActionButtonHover(hover) {
      this.isMoreActionButtonHover = hover
    },
    handleButtonsPosition() {
      const { height, left, top, width } = document.querySelector(".roomChatOutput").getBoundingClientRect();
      const box = this.$refs.actionsBox.getBoundingClientRect();
      const overBottom = box.y + box.height >= top + height;
      const overBorder = left > box.x || (left + width) < (box.x + box.width);
      const isSelf = this.$refs.message.className.includes('self')
      if (!overBorder && !overBottom) { return; }
      if (overBorder) {
        if ((box.y - box.height) > top) {
          this.$refs.actionsBox.style.bottom = `${this.$refs.messageInfo.offsetHeight * 0.5 + 20}px`
        } else {
          this.$refs.actionsBox.style.top = `${this.$refs.messageInfo.offsetHeight * 0.5 + 20}px`
        }
      }
      if (overBottom) {
        this.$refs.actionsBox.style.bottom = `${this.$refs.messageInfo.offsetHeight * 0.5 + 20}px`
      }
      if (isSelf) {
        this.$refs.actionsBox.style.left = `30px`
      } else {
        this.$refs.actionsBox.style.right = `30px`
      }
    },
    ...mapActions(['resendMessage', 'recallMessage', 'sendMessage'])
  }
};



