import axios from 'axios';
import lib from '@/lib';
import { Datetime } from 'vue-datetime';

export default {
  name: 'MdInput',
  props: ['template', 'parentLayout', 'direction'],
  data() {
    return {
      inputData: '',
      disabled: false,
      defaultCountryCode: '',
      error: ''
    };
  },
  components: { Datetime },
  computed: {
    divClass() {
      let cls = [];
      // margin
      cls.push(
        lib.flexMessage.getClassMargin(
          this.template.margin,
          this.parentLayout,
          this.direction
        )
      );

      // action
      if (this.template.action) {
        cls.push('hasAction');
      }

      // flex
      if (this.template.flex) {
        cls.push(`fl${this.template.flex}`);
      }

      // sub-type
      if (this.template.subtype) {
        cls.push(`MdInput-${this.template.subtype}`)
      }

      if (!this.isClient) {
        cls.push('MdInputLock')
      }

      return cls;
    },
    subtype () {
      return this.template.subtype
    },
    placeholder () {
      return this.$t(`Request.Message.${this.subtype}.placeholder`)
    },
    setting () {
      const { getClientApiConfig } = this.$store.state.config;
      const { roomTags: [token], _id: roomId } = this.$store.state.roomActive;
      const { headers, url } = getClientApiConfig();
      return {
        headers,
        url: url && `${url}/customer/info`,
        data: {
          token,
          roomId
        }
      }
    },
    result () {
      const { inputData, subtype } = this;
      switch (subtype) {
        case 'date':
          return { text: `${new Date(inputData).getTime()}` };
        case 'phone':
          return { text: inputData.replace(/\s/g, ''), country: this.$refs.telInput.countryCode }
        default:
          return { text: inputData }
      }
    }
  },
  mounted() {
    this.init();
  },
  watch: {
    template () {
      this.init();
    }
  },
  inject: ['updateAskMessage', 'isClient'],
  methods: {
    init() {
      if (this.subtype === 'date' && this.template.text) {
        this.inputData = new Date(+this.template.text).toISOString();
      } else {
        this.inputData = this.template.text;
      }
      if (this.template.country) {
        this.defaultCountryCode = this.template.country;
      }
      this.disabled = this.template.completed;
    },
    doAction() {
      this.error = this.validation();
      if (!this.error) {
        this.send()
      }
    },
    validation() {
      const { inputData, subtype } = this;
      switch (subtype) {
        case 'email':
          const isEmail = (/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(inputData))
          const isCorrectGoogleFormat = /@gmail.com/.test(inputData) ? !/@gmail.com.\w/.test(inputData) : isEmail;
          return isCorrectGoogleFormat ? '' : this.$t('Request.Message.wrong.format');
        case 'text':
        case 'date':
          return inputData ? '' : this.$t('Request.Message.wrong.format');
        case 'phone':
          const phoneValid = this.$refs.telInput.isValid;
          return phoneValid ? '' : this.$t('Request.Message.wrong.format');
      }
    },
    async send () {
      const { setting, subtype, result } = this;
      if (!setting.url) { return; }
      this.disabled = true;
      try {
        await axios({
          ...setting,
          method: 'POST',
          url: setting.url,
          data: {
            ...setting.data,
            type: subtype,
            data: result.text
          }
        });
        this.updateAskMessage(result);
      } catch (error) {
        console.log(error);
      }
    },
  }
};



