import axios from 'axios';
import { Datetime } from 'vue-datetime';
import { DateTime as formatting } from 'luxon';
import Modal from '@/components/Components/Modal/index.vue';
import CustomInput from '@/components/Components/CustomInput/index.vue';

export default {
  name: 'VoteModal',
  props: ['extra'],
  components: {
    Modal,
    CustomInput,
    Datetime,
  },
  data () {
    return {
      question: '',
      end_at: '',
      options: ['', ''],
      type: '',
      pending: false
    }
  },
  computed: {
    minDatetime () {
      return formatting
        .fromISO(new Date().toISOString())
        .plus({ minute: 30 })
        .toISO();
    },
    disableCreateButton () {
      const { voteData, pending } = this;
      return pending || voteData.option.length < 2 || Object.keys(voteData).some(key => !voteData[key]);
    },
    setting () {
      const { getClientApiConfig } = this.$store.state.config;
      const { endpoint, method } = this.extra;
      const { headers, url } = getClientApiConfig();
      return {
        headers,
        method,
        url: url && url + endpoint,
      }
    },
    voteData () {
      const { question, end_at, options, type } = this;
      const { id, roomTags: [token = ''] } = this.$store.state.roomActive;
      const { data } = this.extra;
      const formattime = end_at && formatting
        .fromISO(new Date(end_at).toISOString());
      return {
        room_id: id,
        end_at: formattime && formattime.toMillis(),
        type,
        token,
        question,
        option: options.filter(Boolean).map(label => ({ label })),
        vote_btn_text: this.$t('Vote or see result'),
        end_at_text: `${formattime && formattime.toFormat('yyyy/MM/dd HH:mm')}`,
        ...data,
      }
    }
  },
  methods: {
    async createNewVote () {
      const { url, headers, method } = this.setting;
      if (this.pending || !url || this.disableCreateButton) { return; }
      this.pending = true;
      await axios({
        method,
        url,
        headers,
        data: this.voteData
      });
      this.$emit('close');
      this.$emit('closeActionsGroup');
      this.$emit('outputScrollToBottom');
    }
  },
  watch: {
    options (val) {
      const lastIndex = val.length - 1;
      if (val[lastIndex] && lastIndex < 14) {
        this.options.push('');
        this.$nextTick(function () {
          this.$refs.optionsRef.scrollTop = (lastIndex + 1) * 50;
        })
      }
    }
  }
}



