<template>
  <img :data-src="url" style="visibility: hidden;" ref="imgEle" />
</template>

<script>
export default {
  name: 'LazyLoad',
  props: {
    url: String
  },
  mounted() {
    const watcher = new IntersectionObserver(this.handleEnterView);
    watcher.observe(this.$refs.imgEle);
  },
  methods: {
    handleEnterView(entries, observer) {
      const [entry] = entries;
      if (entry.isIntersecting) {
        const img = entry.target;
        img.setAttribute('src', img.dataset.src);
        img.removeAttribute('data-src');
        img.style.visibility = '';
        observer.unobserve(img);
      }
    }
  }
};
</script>



