import { mapActions } from 'vuex';
import MdBx from './MdBx/index.vue';
import MdImg from './MdImg/index.vue';
import QuestionnaireModal from '@/components/Components/QuestionnaireModal/index.vue';

export default {
  name: 'Bubble',
  props: ['template'],
  inject: ['extra'],
  data() {
    return {
      openQuestionnaire: false
    };
  },
  components: {
    MdBx,
    MdImg,
    QuestionnaireModal
  },
  computed: {
    direction() {
      return this.template.direction
        ? this.template.direction.toLowerCase()
        : 'ltr';
    },
    token () {
      const { roomTags: [ token ] } = this.$store.state.roomActive;
      return token;
    },
    surveyId () {
      return this.extra.surveyId;
    }
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {},
    getHeight() {
      return this.$refs.item.offsetHeight;
    },
    setHeight(height) {
      this.$refs.item.style.height = `${height}px`;
    },
    doAction(action) {
      if (!action) {
        return;
      }
      switch (action.type.toLowerCase()) {
        case 'uri':
          window.open(action.uri);
          break;
        case 'postback':
          if (action.data === 'questionnaire') {
            this.openQuestionnaire = true;
          } else {
            this.sendActionMessage(action);
          }
          break;
      }
    },
    sendActionMessage(action) {
      let msg = {
        messageType: 'text',
        message: action.displayText || action.text || action.label
      };
      this.sendMessage(msg);
      this.$nextTick(() => {
        this.$emit('scrollToBottom');
      });
    },
    ...mapActions(['sendMessage'])
  }
};



