import lib from '@/lib';
import VoteDetail from './VoteDetail/index.vue';
import CustomInput from '@/components/Components/CustomInput/index.vue';

export default {
  name: 'MdVote',
  props: ['template', 'parentLayout', 'direction'],
  data() {
    return {
      isShowModal: false
    };
  },
  components: {
    VoteDetail,
    CustomInput,
  },
  computed: {
    bxStyle() {
      return { margin: '10px 0 0' };
    },
    moreStyle() {
      return {
        color: 'rgb(147, 147, 147)',
        margin: '15px 0',
      }
    },
    actionStyle() {
      return {
        marginTop: '10px',
        height: '40px',
        color: '#ffffff',
        cursor: 'pointer',
        padding: '0 5px',
        'background-color': '#02b13f',
      }
    },
    inputType() {
      const { type } = this.template;
      switch (type) {
        case 'vote-radio':
          return 'radio';
        case 'vote-checkbox':
          return 'checkbox';
        default:
          return '';
      }
    },
    divClass() {
      let cls = [];

      // height
      if (!this.template.height) {
        this.template.height = 'md';
      }
      cls.push(`Ex${lib.flexMessage.renderSizeStr(this.template.height)}`);

      // gravity
      let gravity = this.template.gravity
        ? this.template.gravity.toLowerCase()
        : 'top';
      if (gravity === 'bottom') {
        cls.push('grvB');
      } else if (gravity === 'center') {
        cls.push('grvC');
      }

      // flex
      if ('flex' in this.template) {
        cls.push(`fl${this.template.flex}`);
      }

      // style
      let styleArrayS = ['1', '2', 'L'];
      let styleArrayB = ['primary', 'secondary', 'link'];
      let style = this.template.style
        ? this.template.style.toLowerCase()
        : 'link';
      cls.push(`ExBtn${styleArrayS[styleArrayB.indexOf(style)]}`);

      // margin
      cls.push(
        lib.flexMessage.getClassMargin(
          this.template.margin,
          this.parentLayout,
          this.direction
        )
      );

      // action
      if (this.template.action) {
        cls.push('hasAction');
      }

      return cls;
    }
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {},
    doAction() {
      this.isShowModal = true;
    }
  }
};



