<template>
  <div v-tooltip="$t('Tooltip.Translate')">
    <span
      class="actionBtn"
      :class="{
        pcHide: !action.pcEnable,
        padHide: !action.padEnable,
        mobileHide: !action.mobileEnable
      }"
      :title="$t(action.i18nKey)"
      @click="toggle"
    >
      <Icon :name="(roomActive.pref || {}).translation ? 'translation': 'translation-disabled'" :class="{'disabled': disabled}"></Icon>
    </span>
  </div>
</template>

<script>

export default {
  name: 'Translation',
  props: ['action'],

  computed: {
    roomActive() {
      return this.$store.state.roomActive;
    },
    disabled() {
      return false;
      // return ((this.roomActive || {}).pref || {}).translation === null;
    }
  },
  methods: {
    toggle() {
      this.$store.dispatch('updateRoomPref', {
        roomId: this.roomActive._id,
        pref: {
          translation: !this.roomActive.pref.translation
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.disabled {
    cursor: not-allowed;
}
</style>



