import { mapActions } from 'vuex';
import Swiper from 'swiper/dist/js/swiper.js';
export default {
  name: 'TemplateCarousel',
  props: ['template', 'contentWidth'],
  watch: {
    contentWidth(val, oldval) {
      this.showCarouselCtrlBtn = this.checkWidth();
    }
  },
  data() {
    return {
      imageInnerWidth: window.innerWidth,
      showCarouselCtrlBtn: true
    };
  },
  components: {},
  computed: {
    columns() {
      return this.template.columns || [this.template];
    },
    slideInnerStyle() {
      return {
        flex: '1 0 auto',
        width: `${window.innerWidth * 0.66}px`,
        'max-width': '300px',
      };
    },
    currentLang () {
      const { lang } = this.$store.state.config;
      const langMap = {
        en: 'en',
        'zh-tw': 'zh',
        'zh-cn': 'cn',
        ja: 'ja',
        fr: 'fr',
        kr: 'kr'
      };
      return langMap[lang];
    },
    langOrder () {
      const { currentLang } = this;
      const defaultOrder = ['en', 'zh', 'cn', 'ja', 'fr', 'kr'];
      return [currentLang]
        .concat(
          defaultOrder.filter(lang => lang !== currentLang)
        );
    }
  },
  mounted() {
    this.init();
    this.showCarouselCtrlBtn = this.checkWidth();
    window.addEventListener('resize', () => {
      this.imageInnerWidth = window.innerWidth;
    });
  },
  methods: {
    templateTitle(column) {
      const { langOrder } = this;
      for (const idx in langOrder) {
        const langData = column[langOrder[idx]]
        if (langData && langData.title) {
          return langData.title
        }
      }
      return column.title
    },
    templateText(column) {
      const { langOrder } = this;
      for (const idx in langOrder) {
        const langData = column[langOrder[idx]]
        if (langData && langData.text) {
          return langData.text
        }
      }
      return column.text
    },
    checkWidth() {
      if (!this.template.columns || this.template.columns.length <= 1) {
        return false;
      }
      return this.template.columns.length * 300 >= this.contentWidth;
    },
    init() {
      let titleMaxH = this.$refs.title.reduce((prev, item) => {
        return prev > item.offsetHeight ? prev : item.offsetHeight;
      }, 0);
      this.$refs.title.forEach(item => {
        item.style.height = `${titleMaxH}px`;
      });
      let textMaxH = this.$refs.text.reduce((prev, item) => {
        return prev > item.offsetHeight ? prev : item.offsetHeight;
      }, 0);
      this.$refs.text.forEach(item => {
        item.style.minHeight = `${textMaxH}px`;
      });

      this.swiperMain = new Swiper(this.$refs.swiper, {
        freeMode: true,
        slidesPerView: 'auto',
        spaceBetween: 10,
        watchOverflow: true
      });
    },
    sendActionMessage(action) {
      let msg = {
        messageType: 'text',
        message: action.text || action.displayText || action.label
      };
      this.sendMessage(msg);
      this.$nextTick(() => {
        this.$emit('scrollToBottom');
      });
    },
    next() {
      this.swiperMain.slideNext();
    },
    prev() {
      this.swiperMain.slidePrev();
    },
    ...mapActions(['sendMessage'])
  }
};



