
import Tag from '../Tag/index.vue';

export default {
  name: 'SetColor',
  props: {
    currentColor: String
  },
  components: {
    Tag,
  },
  computed: {
    colorStyle () {
      return { backgroundColor: this.currentColor };
    },
    inputStyle () {
      return { width: 0, flex: '1 0 auto', margin: 0, padding: '6px 16px', borderRaidus: '5px', border: 'none', fontSize: '14px' };
    },
    defaultColors () {
      return this.$store.state.config.popularTagColors
    },
    inputColor: {
      get () {
        return this.currentColor;
      },
      set (color = '') {
        this.$emit('change', color);
      }
    }
  },
  methods: {
  }
};



