import axios from 'axios';
import { DateTime } from 'luxon';
import Modal from '@/components/Components/Modal/index.vue';
import CustomInput from '@/components/Components/CustomInput/index.vue';

export default {
  name: 'VoteDetail',
  components: {
    Modal,
    CustomInput
  },
  mounted () {
    this.getVoteDetail();
  },
  data () {
    return {
      pending: false,
      detail: {
        end_at: '',
        vote_options: [],
      },
    }
  },
  inject: ['extra'],
  computed: {
    endAtText () {
      const { detail: { end_at } } = this;
      if (end_at) {
        return `${this.$t('deadline')} ` + DateTime.fromISO(end_at)
          .toFormat('yyyy/MM/dd HH:mm');
      }
      return '';
    },
    totalCount () {
      const { vote_options } = this.detail;
      return vote_options.reduce((prev, curr) => prev + curr.count, 0);
    },
    countSpanStyle () {
      return { float: 'right', fontSize: '14px' };
    },
    isExpired () {
      const { detail: { end_at } } = this;
      return new Date() > new Date(end_at);
    },
    disableConfirmButton () {
      const { detail: { vote_options }, pending } = this;
      const isSelected = vote_options.some(option => option.is_select);
      return this.isExpired || !isSelected || pending;
    },
    setting () {
      const { getClientApiConfig } = this.$store.state.config;
      const { headers, url } = getClientApiConfig();
      return {
        headers,
        url: url && url + '/vote'
      }
    },
    params () {
      const { roomTags: [ token ] } = this.$store.state.roomActive;
      return { token, vote_id: this.extra.voteId };
    }
  },
  methods: {
    convertCountToPercentage (count) {
      return (100 * (count / this.totalCount) || 0).toFixed(1) || 0;
    },
    handleRadioCountUpdate (id) {
      return (option, idx) => {
        const isTarget = option.id === id;
        const isChanged = !isTarget && option.is_select;
        this.$set(this.detail.vote_options[idx], 'is_select', isTarget);
        this.$set(this.detail.vote_options[idx], 'count', isChanged ? option.count - 1 : (isTarget ? option.count + 1 : option.count));
      };
    },
    handleCheckboxCountUpdate (id) {
      return (option, idx) => {
        const isTarget = option.id === id;
        const isChanged = option.is_select;
        if (isTarget) {
          const { is_select, count } = option
          this.$set(this.detail.vote_options[idx], 'is_select', !is_select);
          this.$set(this.detail.vote_options[idx], 'count', isChanged ? count - 1 : count + 1);
        }
      };
    },
    handleOptionClick (id) {
      const { type, vote_options } = this.detail;
      switch (type) {
        case 'radio':
          vote_options.forEach(this.handleRadioCountUpdate(+id));
          break;
        case 'checkbox':
          vote_options.forEach(this.handleCheckboxCountUpdate(+id));
          break;
      }
    },
    getVoteDetail () {
      const { url, headers } = this.setting;
      if (!url) { return; }
      axios.get(
        url,
        { params: this.params, headers },
      ).then(({ data }) => {
        this.detail = { ...data.data.data };
      });
    },
    updateVoteDetail () {
      const { vote_options, id } = this.detail;
      const { url, headers } = this.setting;
      this.pending = true;
      const ids = vote_options
        .filter(option => option.is_select)
        .map(option => option.id);

      axios.put(
        url,
        { vote_option: ids, vote_id: id },
        { headers },
      ).then(() => this.$emit('close'));
    }
  }
}



