import './style/app.scss';
import './style/color.scss';
import { mapState } from 'vuex';
export default {
  name: 'App',
  computed: {
    ...mapState(['socket'])
  },
  mounted() {
    document.addEventListener('visibilitychange', () => {
      if (document.hidden) {
        this.socket.disconnect();
      } else {
        window.ImkitJsSDK.setSocket();
      }
    });
  }
};



