import axios from 'axios';
import Modal from '@/components/Components/Modal/index.vue';

export default {
  name: 'Questionare',
  props: ['action'],
  data() {
    return {
      pending: false,
      isShowModal: false
    };
  },
  components: { Modal },
  directives: {},
  computed: {
    extra() {
      return this.action.extra;
    },
    setting () {
      const { getClientApiConfig } = this.$store.state.config;
      const { endpoint, method } = this.extra;
      const { roomType, _id } = this.$store.state.roomActive;
      const { headers, url } = getClientApiConfig();
      const buttonText = roomType === 'group' ? `&buttonText=${this.$t('Fill out the survey')}` : ''
      return {
        headers,
        method,
        url: url && `${url}${endpoint}?roomId=${_id}${buttonText}`,
      }
    },
    link () {
      const { roomTags } = this.$store.state.roomActive;
      return `/dashboard/enterpoint.html?token=${roomTags[0]}`
    }
  },
  methods: { 
    async send() {
      this.$emit('outputScrollToBottom');
      if (this.pending || !this.setting.url) { return; }
      this.pending = true;
      try {
        await axios(this.setting);
      } catch (error) {
        this.isShowModal = true
      }
      this.pending = false;
    }
  }
};



