export default {
  name: 'RichMenus',
  props: ['list', 'openRichMenus'],
  data() {
    return {
      itemHeight: 100,
      maxHeight: 250
    };
  },
  mounted() {
    this.getItemHeight();
    window.addEventListener('resize', () => {
      this.getItemHeight();
    });
  },
  components: {},
  methods: {
    clickItem(item) {
      const { type, sendText, url } = item;
      if (type === 'text' && sendText) {
        this.$emit('sendMessage', {
          messageType: 'text',
          message: sendText,
          reply: null
        });
      } else if (type === 'url' && url) {
        window.open(url, '_blank');
      }
    },
    getItemHeight() {
      this.$nextTick(() => {
        setTimeout(() => {
          this.itemHeight = Math.ceil((this.$refs.richMenus.clientWidth - 2) / 3);
        }, 0);
      });
    }
  }
};



