export default {
  name: 'AudioPlayer',
  props: ['file'],
  data() {
    return {
      isPlaying: false,
      currentTime: 0,
      duration: 1,
    };
  },
  computed: {
    currentDurationWidth () {
      return {
        width: `${(this.currentTime / this.duration) * 100}%`
      };
    },
    currentDuration () {
      return this.formateDuration(this.duration - this.currentTime);
    }
  },
  watch: {
    isPlaying (val) {
      if (val) {
        this.$refs.audio.play();
      } else {
        this.$refs.audio.pause();
      }
    }
  },
  mounted () {
    this.$refs.audio.addEventListener('timeupdate', this.handleTimeUpdate);
    this.$refs.audio.addEventListener('ended', this.handleAudioEnd);
  },
  methods: {
    formateDuration (duration) {
      const min = Math.floor(duration / 60);
      const sec = Math.floor(duration % 60);
      return [
        min > 10 ? min : `0${min}`,
        sec > 10 ? sec : `0${sec}`
      ].join(':');
    },
    handleAudioPlay () {
      this.isPlaying = !this.isPlaying;
    },
    handleTimeUpdate (e) {
      this.currentTime = e.target.currentTime;
    },
    getDuration () {
      this.duration = this.$refs.audio.duration;
    },
    handleAudioEnd () {
      this.$refs.audio.currentTime = 0;
      this.isPlaying = false;
    }
  }
};



