import VoteModal from './VoteModal/index.vue';

export default {
  name: 'Vote',
  props: ['action'],
  components: {
    VoteModal
  },
  data () {
    return {
      isShowModal: false,
    }
  },
  computed: {
    extra () {
      return this.action.extra;
    }
  },
  methods: {
    click () {
      this.isShowModal = true;
    },
  }
}



