import config from '@/config.js';
import events from '@/events.js';
export default {
  vue: null,
  config,
  events,
  // window 祖先，拉最上層改 title 及判斷分頁是否檢視中用
  pageAncestor: {
    el: null, // 祖先元件
    title: 'IMLit Demo' // 預設的 title
  },
  pageLoading: true, // 打開頁面時顯示載入中，直到列表載入完成
  api: {},
  socket: {},
  connect: true, // 是否與 socket 連接，判斷斷線用，初始設 true 避免頁面一載入就顯示斷線訊息
  i18n: {},
  alert: null,
  confirm: null,
  roomList: [],
  roomFolderList: [], // 房間資料夾
  roomListLoader: true, // 是否顯示圈圈
  roomListSearchResult: null, // 列表搜尋結果
  roomListSearchMessages: null, // 列表搜尋結果(訊息)
  roomChatList: [],
  roomChatRight: '', // search 之類要顯示在聊天室右邊的東西
  roomChatSearch: {
    showSearchBar: false,
    showInput: false,
    keyword: '',
    resultCancelSource: null,
    result: [],
    indexOfResult: 0,
    messagesCancelSource: null,
    messages: [],
    showMessages: false,
    hasBefore: false,
    hasAfter: false,
    loadBefore: false,
    loadAfter: false
  },
  roomActive: {},
  sendingList: [],
  client: {},
  clientPref: [],
  forward: {
    messages: [],
    rooms: [],
    selectMessages: false,
    selectRooms: false,
    sending: false
  },
  typingList: [],
  supportNotification: false, // 是否支援推播
  enableNotification: false, // 是否開啟推播
  isMobile: false, // 是否有 touch 事件
  widthPad: 1024, // Pad 版對大寬度
  widthPhone: 640 // Phone 版對大寬度
};



