import ClickOutside from 'vue-click-outside';

export default {
  name: 'Modal',
  props: ['isShowClose', 'isOutSideClose'],
  data() {
    return {};
  },
  directives: {
    ClickOutside
  },
  computed: {},
  methods: {
    outSideClose() {
      this.isOutSideClose && this.close();
    },
    close() {
      this.$emit('close');
    }
  }
};



