import { mapActions } from 'vuex';
import AudioPlayer from '@/components/Components/AudioPlayer/index.vue';
export default {
  name: 'FileUploader',
  props: [
    'id',
    'cancelSource',
    'type',
    'url',
    'width',
    'height',
    'progress',
    'extra',
    'message',
    'isThumbnail'
  ],
  data() {
    return {};
  },
  mounted() {},
  components: {
    AudioPlayer
  },
  computed: {
    parseExtra() {
      if (typeof this.extra === 'object' && this.extra !== null) {
        return this.extra;
      } else if (this.extra) {
        return JSON.parse(this.extra) || {};
      } else {
        return {};
      }
    },
    icon() {
      if (this.isVideoThumbnail || this.type === 'video') {
        return 'file-video';
      } else if (this.type === 'image') {
        return 'file-image';
      } else if (this.type === 'audio') {
        return 'file-audio';
      } else if (this.parseExtra.fileExtension === 'pdf') {
        return 'file-pdf';
      }
      return 'file-alt';
    },
    fileSize() {
      if (!this.parseExtra.bytes) {
        return 0;
      }
      let bytes = this.parseExtra.bytes;
      let thresh = 1024;
      if (Math.abs(bytes) < thresh) {
        return bytes + ' B';
      }
      let units = ['kB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];
      var u = -1;
      do {
        bytes /= thresh;
        ++u;
      } while (Math.abs(bytes) >= thresh && u < units.length - 1);
      return bytes.toFixed(1) + ' ' + units[u];
    },
    imageMaxWidth() {
      return this.isThumbnail
        ? this.$store.state.config.chat.thumbnailSize + 'px'
        : null;
    },
    convertHeight() {
      const { width, height } = this;
      const { isMobile } = this.$store.state;
      const maxWidth = isMobile ? 190 : 230;
      if (width && height) {
        return width > maxWidth ? height * (maxWidth / width) : height
      }
      return ''
    }
  },
  methods: {
    cancel() {
      this.uploadFileCancel({
        id: this.id,
        cancelSource: this.cancelSource
      });
    },
    ...mapActions(['uploadFileCancel'])
  }
};



