export default {
  name: 'CustomInput',
  props: [
    'val',
    'type',
    'name',
    'value',
    'checked',
    'disabled'
  ],
  data() {
    return {};
  },
  components: {},
  methods: {
    handleInput (event) {
      this.$emit('input', event.target.value);
    }
  }
};



