import axios from 'axios';
import Modal from '@/components/Components/Modal/index.vue';
import CustomInput from '@/components/Components/CustomInput/index.vue';

export default {
  name: 'QuestionnaireModal',
  props: {
    token: String,
    surveyId: Number
  },
  components: {
    Modal,
    CustomInput
  },
  data () {
    return {
      pending: false,
      complete: false,
      questions: [],
      results: []
    }
  },
  mounted () {
    this.getQuestionnaireDetail();
  },
  computed: {
    isReadyToSubmit () {
      const { results } = this
      return !results.every(Boolean)
    },
    setting () {
      const { surveyId } = this;
      const { getClientApiConfig } = this.$store.state.config;
      const { headers, url } = getClientApiConfig();
      if (!surveyId) {
        return {}
      }
      return {
        headers,
        method: 'GET',
        url: url && `${url}/survey/${surveyId}`
      }
    },
    questionnaireResult () {
      const { questions, results } = this
      return questions.map((question, idx) => ({
        id: question.id,
        type: question.type,
        data: question.type === 'rating'
          ? this.$t(
            '{count} star rating',
            { count: results[idx] },
          )
          : results[idx]
      }))
    },
    lang () {
      const { lang } = this.$store.state.config;
      switch (lang) {
        case 'zh':
        case 'zh-tw':
          return 'zh';
        case 'cn':
        case 'zh-cn':
          return 'cn';
        case 'ko':
        case 'kr':
          return 'kr';
        default:
          return lang
      }
    }
  },
  methods: {
    initResults () {
      const { questions } = this
      this.results = new Array(questions.length).fill('')
    },
    handleRating (idx, val) {
      const { results } = this;
      results[idx] = val;
      this.results = [...results];
    },
    isStarFill (idx, val) {
      return this.results[idx] !== '' && this.results[idx] >= val;
    },
    async getQuestionnaireDetail () {
      const { setting, token } = this;
      this.pending = true
      try {
        const { data } = await axios({ ...setting, url: `${setting.url}?token=${token}` });
        const { data: { surveyFlow } } = data.data
        this.questions = surveyFlow
        this.initResults();
      } catch (error) {
        console.log(error);
      } finally {
        this.pending = false;
      }
    },
    async sendQuestionnaireResult () {
      const { questionnaireResult, setting } = this
      setting.method = 'POST'
      this.pending = true
      try {
        if (!setting.url) {
          throw new Error('surveyId not found');
        }
        await axios({
          ...setting,
          method: 'POST',
          url: `${setting.url}/result`,
          data: { result: questionnaireResult }
        });
        this.complete = true;
      } catch (error) {
        console.log(error);
      } finally {
        this.pending = false;
      }
    }
  }
}



