import axios from 'axios';
import Modal from '@/components/Components/Modal/index.vue';

export default {
  name: 'Ask',
  props: ['action'],
  data() {
    return {
      pending: false,
      isShowModal: false,
      selectedType: '',
      title: '',
      text: '',
      tmp: {}
    };
  },
  components: { Modal },
  computed: {
    filled () {
      return this.title && this.text;
    },
    updated () {
      const { title, text, tmp, selectedType } = this
      const field = selectedType === 'text' ? 'other' : selectedType;
      const tmpTitle = tmp[`${field}_title`] || '';
      const tmpText = tmp[`${field}_text`] || '';
      return tmpTitle !== title || tmpText !== text;
    },
    extra () {
      return this.action.extra;
    },
    link () {
      const { roomTags: [token] } = this.$store.state.roomActive;
      return `/dashboard/enterpoint.html?token=${token}&tab=client`
    },
    setting () {
      const { getClientApiConfig } = this.$store.state.config;
      const { roomTags: [token], _id: roomId } = this.$store.state.roomActive;
      const { endpoint } = this.extra;
      const { selectedType, title, text } = this;
      const { headers, url } = getClientApiConfig();
      return {
        headers,
        url: url && `${url}${endpoint}`,
        data: {
          token,
          roomId,
          type: selectedType,
          title,
          text
        }
      }
    }
  },
  watch: {
    isShowModal (val) {
      val && this.init();
    }
  },
  methods: {
    async init () {
      const {
        setting,
        pending
      } = this;
      const { token } = setting.data;
      if (pending || !setting.url) { return; }
      try {
        const { data } = await axios({
          ...setting,
          method: 'GET',
          url: `${setting.url}/type`,
          params: { token }
        });
        this.tmp = data.data;
      } catch (error) {
        console.log(error);
      }
    },
    async send () {
      const {
        setting,
        pending
      } = this;
      if (pending || !setting.url) { return; }
      this.pending = true;
      try {
        await axios({
          ...setting,
          method: 'GET',
          url: `${setting.url}/trigger`,
          params: setting.data
        });
      } catch (error) {
        console.log(error);
      } finally {
        this.pending = false;
        this.handleClose();
      }
    },
    async save () {
      const {
        setting,
        pending,
      } = this;
      if (pending || !setting.url) { return; }
      this.pending = true;
      try {
        await axios({
          ...setting,
          method: 'POST',
          url: `${setting.url}/type`
        });
      } catch (error) {
        console.log(error);
      } finally {
        const field = setting.data.type === 'text' ? 'other' : setting.data.type;
        this.tmp = {
          ...this.tmp,
          [`${field}_title`]: setting.data.title,
          [`${field}_text`]: setting.data.text
        };
      }
      this.pending = false;
    },
    handleSelectType (type) {
      this.selectedType = type;
      const field = type === 'text' ? 'other' : type;
      this.title = this.tmp[`${field}_title`] || this.$t(`Request.Message.${field}`) || '';
      this.text = this.tmp[`${field}_text`] || this.$t(`Request.Message.${field}.text`) || '';
    },
    handlePrev () {
      this.selectedType = '';
      this.text = '';
      this.title = '';
    },
    handleClose () {
      this.isShowModal = false;
      this.handlePrev();
    }
  }
};



