import { mapActions } from 'vuex';
import _ from 'lodash';
import Carousel from './Carousel/index.vue';
import Bubble from './Bubble/index.vue';

export default {
  name: 'Flex',
  props: ['message'],
  data() {
    return {};
  },
  components: {
    Carousel,
    Bubble
  },
  computed: {
    template() {
      return this.message.contents;
    },
    isClient() {
      const { id } = this.message.sender;
      const clientId = this.$store.state.client.client;
      return id !== clientId;
    }
  },
  provide() {
    return {
      extra: this.message.extra || {},
      updateRating: this.updateRating,
      isClient: this.isClient,
      updateAskMessage: this.updateAskMessage
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {},
    updateRating(rating) {
      if (!this.isClient) {
        return;
      }
      const ratingContents = _.get(this.message, 'contents.body.contents[1].contents');
      let msg = {
        messageType: this.message.messageType,
        _id: this.message._id,
        contents: _.get(this.message, 'contents'),
      }
      _.set(
        msg,
        'contents.body.contents[1].contents',
        ratingContents.map(content => ({
          ...content,
          action: {
            text: content.action.text,
            fill: +content.action.text <= +rating
          },
        }))
      )
      this._sendMessage({
        roomId: this.message.room,
        message: msg,
        sendingMessage: {}
      });
    },
    updateAskMessage(content) {
      if (!this.isClient) {
        return;
      }
      const path = 'contents.body.contents[1].contents';
      const askContents = _.get(this.message, path);
      askContents[0] = {
        ...askContents[0],
        ...content,
        completed: true
      }
      let msg = {
        messageType: this.message.messageType,
        _id: this.message._id,
        contents: _.get(this.message, 'contents'),
      }
      _.set(msg, path, askContents)
      this._sendMessage({
        roomId: this.message.room,
        message: msg,
        sendingMessage: {}
      });
    },
    sendActionMessage(action) {
      let msg = {
        messageType: 'text',
        message: action.label
      };
      this.sendMessage(msg);
      this.$nextTick(() => {
        this.$emit('scrollToBottom');
      });
    },
    ...mapActions(['sendMessage', '_sendMessage'])
  }
};



