import store from '@/store';
import filters from '@/filters';
import i18n from '@/i18n';

let urlRegex = /(https?:\/\/)?[a-z0-9.]{1,256}\.[a-z]{2,6}\b([-a-zA-Z0-9()@:%_+.~#?&//=]*)/i;
let phoneRegex = /\d{8,256}/i;
let emailRegex = /([A-Za-z0-9_\-.])+@([A-Za-z0-9_\-.])+\.([A-Za-z]{2,4})/i;

class Message {
  systemTypes = [
    'joinRoom',
    'leaveRoom',
    'addMember',
    'addMembers',
    'deleteMember',
    'recall'
  ];
  // 補足缺少的 key，缺少的 key 會導致資料更新時無法同步渲染
  async fullKeys(message) {
    if (message.messageType === 'line') {
      message.fromLine = true;
      try {
        this.lineMessage(message);
      } catch (e) {
        message.messageType = 'text';
        message.message = 'line message';
      }
    }
    let keys = ['groupNext', 'groupPrev', 'translatedText'];
    for (let key of keys) {
      if (!(key in message)) {
        message[key] = null;
      }
    }
    message.message = message.message || '';

    // 把有 url 的 textType 轉成urlType
    let messageList = this.textViewMessageList(message);
    this.textToUrlType(message, messageList);
    this.translate(message);
  }

  async translate(message) {
    if (
      message.sender._id !== store.state.client.client &&
      message.messageType === 'text' &&
      store.state.config.translation.apiKey &&
      store.state.config.translation.enable &&
      (store.state.roomActive.pref && store.state.roomActive.pref.translation)
    ) {
      const myHeaders = new Headers();
      myHeaders.append('Content-Type', 'application/x-www-form-urlencoded');

      const urlencoded = new URLSearchParams();

      const langsGoogleSupported = ['am', 'ar', 'eu', 'bn', 'en-GB', 'pt-BR', 'bg', 'ca', 'chr', 'hr', 'cs', 'da', 'nl', 'en', 'et', 'fil', 'fi', 'fr', 'de', 'el', 'gu', 'iw', 'hi', 'hu', 'is', 'id', 'it', 'ja', 'kn', 'ko', 'lv', 'lt', 'ms', 'ml', 'mr', 'no', 'pl', 'pt-PT', 'ro', 'ru', 'sr', 'zh-CN', 'sk', 'sl', 'es', 'sw', 'sv', 'ta', 'te', 'th', 'zh-TW', 'tr', 'ur', 'uk', 'vi', 'cy'];
      const index = langsGoogleSupported.findIndex((lang) => lang.toLowerCase() === navigator.language.toLowerCase());
      if (index > -1) {
        urlencoded.append('target', langsGoogleSupported[index]);
      } else {
        urlencoded.append('target', navigator.language.split('-')[0]);
      }
      urlencoded.append('q', message.message);
      urlencoded.append('key', store.state.config.translation.apiKey);

      const requestOptions = {
        method: 'POST',
        headers: myHeaders,
        body: urlencoded,
        redirect: 'follow'
      };
      fetch('https://translation.googleapis.com/language/translate/v2', requestOptions)
        .then(response => {
          return response.json();
        })
        .then(jsonData => {
          const translatedText = jsonData.data && jsonData.data.translations[0].translatedText;
          if (translatedText !== message.message) {
            message.translatedText = translatedText;
          }
        })
        .catch(err => {
          console.error('錯誤:', err);
        });
    }
  }

  // 把 line 訊息轉成 IMKit 訊息
  lineMessage(message) {
    if (message.messageType !== 'line' || !message.extra) {
      return;
    }
    let extra = JSON.parse(message.extra);
    if (extra.quickReply) {
      message.quickReply = extra.quickReply;
    }
    switch (extra.type) {
      case 'text':
        message.messageType = 'text';
        message.message = extra.text;
        message.extra = null;
        break;
      case 'sticker':
        message.messageType = 'text';
        message.message = `sticker-${extra.packageId}-${extra.stickerId}`;
        message.extra = null;
        break;
      case 'location':
        message.messageType = 'location';
        message.message = `${extra.title}\n${extra.address}`;
        message.latitude = extra.latitude;
        message.longitude = extra.longitude;
        message.extra = null;
        break;
      case 'template':
        message.messageType = 'template';
        message.template = extra.template;
        message.extra = null;
        break;
      case 'flex':
        message.messageType = 'flex';
        message.contents = extra.contents;
        message.extra = null;
        break;
      case 'audio':
        if (extra.contentProvider.type === 'external') {
          message.messageType = 'audio';
          message.originalUrl = extra.contentProvider.originalContentUrl;
          message.duration = Math.floor(extra.duration / 1000);
          message.extra = '{}';
        } else {
          message.messageType = 'text';
          message.message = 'line audio message';
          message.extra = null;
        }
        break;
      case 'image':
        if (extra.contentProvider.type === 'external') {
          message.messageType = 'image';
          message.originalUrl = extra.contentProvider.originalContentUrl;
          message.thumbnailUrl = extra.contentProvider.previewImageUrl;
          message.extra = '{}';
        } else {
          message.messageType = 'text';
          message.message = 'line image message';
          message.extra = null;
        }
        break;
      case 'video':
        if (extra.contentProvider.type === 'external') {
          message.messageType = 'video';
          message.originalUrl = extra.contentProvider.originalContentUrl;
          message.thumbnailUrl = extra.contentProvider.previewImageUrl;
          message.duration = Math.floor(extra.duration / 1000);
          message.extra = '{}';
        } else {
          message.messageType = 'text';
          message.message = 'line video message';
          message.extra = null;
        }
        break;
      default:
        message.messageType = 'text';
        message.message = 'line message';
        break;
    }
  }

  // 把有 url 的 textType 轉成urlType
  async textToUrlType(message, messageList) {
    if (message.messageType === 'text') {
      let urls = messageList.filter(obj => {
        return obj.type === 'url';
      });
      if (urls && urls.length) {
        let originalUrl = [];
        let extra = message.extra ? JSON.parse(message.extra) || {} : {};
        extra.urls = [];
        let urlCache = [];
        try {
          urlCache = JSON.parse(sessionStorage.getItem('IMKit-urlCache')) || [];
        } catch (error) {
          console.error(error);
        }
        for (let obj of urls) {
          let url = obj.url;
          // 有在cache裡，直接用，沒有再撈
          let urlData = null;
          let find = urlCache.find(item => {
            return item.url === url;
          });
          if (find) {
            urlData = find.data;
          } else {
            urlData = await store.state.api.url.getPreview(url);
            urlCache.push({
              url: url,
              data: urlData
            });
          }
          if (urlData && urlData.Url) {
            originalUrl.push(url);
            extra.urls.push(urlData);
          }
        }
        try {
          sessionStorage.setItem('IMKit-urlCache', JSON.stringify(urlCache));
        } catch (error) {
          console.error(error);
        }

        message.messageType = 'url';
        message.originalUrl = JSON.stringify(originalUrl);
        message.extra = JSON.stringify(extra);

        store.commit('checkScrollToBottom');
      }
    }
  }
  // 收到的訊息處理
  textView(message) {
    let messageList = this.textViewMessageList(message);
    return messageList
      .map(obj => {
        return obj.value;
      })
      .join('').trim();
  }

  parseMentions(users, message, text) {
    if (text.length === 0) return [];
    if (users.length === 0) return this.doEmailRegex(message, text);
    let user = users[0];
    if (!text.includes(`@${user._id}`)) return this.parseMentions(users.slice(1), message, text);
    const object = {
      type: 'tag',
      value: `<span class="textTag" data-uid="${btoa(
        user._id
      )}">@${this.XSS(user.nickname) ||
        i18n.t('Unknown Name')}</span>`
    };
    return text.split(`@${user._id}`).reduce((a, c, i, array) => a.concat(this.parseMentions(users.slice(1), message, c), i < array.length - 1 ? [object] : []), []);
  }

  textViewMessageList(message) {
    return this.parseMentions(store.state.roomActive.members, message, message.message);
  }

  doEmailRegex(message, str) {
    let messageList = [];
    let searchIdx = str.search(emailRegex);
    while (searchIdx >= 0) {
      if (searchIdx > 0) {
        messageList = messageList.concat(
          this.doUrlRegex(message, str.substr(0, searchIdx))
        );
        str = str.substr(searchIdx);
      }
      let link = str.match(emailRegex);
      if (typeof link === 'object') {
        link = link[0];
      }
      messageList.push({
        type: 'email',
        value: `<a href="mailto:${link}" target="_blank">${this.doHighlight(
          message,
          link
        )}</a>`
      });
      str = str.substr(link.length);
      searchIdx = str.search(emailRegex);
    }
    messageList = messageList.concat(this.doUrlRegex(message, str));
    return messageList;
  }

  doUrlRegex(message, str) {
    let messageList = [];
    let searchIdx = str.search(urlRegex);
    while (searchIdx >= 0) {
      if (searchIdx > 0) {
        messageList = messageList.concat(
          this.doPhoneRegex(message, str.substr(0, searchIdx))
        );
        str = str.substr(searchIdx);
      }
      let link = str.match(urlRegex);
      if (typeof link === 'object') {
        link = link[0];
      }
      let url = link;
      if (link.substr(0, 4).toLowerCase() !== 'http') {
        url = `http://${link}`;
      }
      messageList.push({
        type: 'url',
        url: url,
        value: `<a href="${url}" target="_blank">${this.doHighlight(
          message,
          link
        )}</a>`
      });
      str = str.substr(link.length);
      searchIdx = str.search(urlRegex);
    }
    messageList = messageList.concat(this.doPhoneRegex(message, str));
    return messageList;
  }

  doPhoneRegex(message, str) {
    let messageList = [];
    let searchIdx = str.search(phoneRegex);
    while (searchIdx >= 0) {
      if (searchIdx > 0) {
        messageList.push({
          type: 'text',
          value: this.doHighlight(message, str.substr(0, searchIdx))
        });
        str = str.substr(searchIdx);
      }
      let link = str.match(phoneRegex);
      if (typeof link === 'object') {
        link = link[0];
      }
      messageList.push({
        type: 'tel',
        value: `<a href="tel:${link}" target="_blank">${this.doHighlight(
          message,
          link
        )}</a>`
      });
      str = str.substr(link.length);
      searchIdx = str.search(phoneRegex);
    }
    messageList.push({
      type: 'text',
      value: this.doHighlight(message, str)
    });
    return messageList;
  }

  doHighlight(message, str) {
    let messageList = [];

    let roomChatSearch = store.state.roomChatSearch;
    if (
      roomChatSearch.keyword &&
      roomChatSearch.result.length &&
      roomChatSearch.result[roomChatSearch.indexOfResult] === message._id
    ) {
      let keywordRegex = new RegExp(roomChatSearch.keyword, 'i');
      let searchIdx = str.search(keywordRegex);
      while (searchIdx >= 0) {
        if (searchIdx > 0) {
          messageList.push({
            type: 'text',
            value: this.XSS(str.substr(0, searchIdx))
          });
          str = str.substr(searchIdx);
        }
        let highlight = str.match(keywordRegex);
        if (typeof highlight === 'object') {
          highlight = highlight[0];
        }
        messageList.push({
          type: 'highlight',
          value: `<span class="highlight">${this.XSS(highlight)}</span>`
        });
        str = str.substr(highlight.length);
        searchIdx = str.search(keywordRegex);
      }
      messageList.push({
        type: 'text',
        value: this.XSS(str)
      });
    } else {
      messageList.push({
        type: 'text',
        value: this.XSS(str)
      });
    }
    return messageList
      .map(obj => {
        return obj.value;
      })
      .join('');
  }

  // XSS
  XSS(str) {
    return str
      .replace(/</g, '&lt;')
      .replace(/>/g, '&gt;')
      .replace(/\r\n/g, '<br />')
      .replace(/\n/g, '<br />');
  }

  // 顯示訊息跨日時間
  messagesTimespan(messages) {
    let isFirst = true;
    let timespan = '';
    for (let message of messages) {
      message.timespan = null;
      if (message.messageTimeMS) {
        let _timespan = filters.timeDiff(message.messageTimeMS, true);
        if (_timespan !== timespan) {
          message.timespan = _timespan;
          // 如果是今天，後面就不用算了
          // 如果是今天而且是第一個 timespan，就不顯示
          if (_timespan === i18n.t('Today')) {
            if (isFirst) {
              message.timespan = null;
            }
            break;
          }
          timespan = _timespan;
          isFirst = false;
        }
      }
    }
  }

  // 訊息群組，同一人短時間發送的訊息包成群組
  messagesGroup(messages) {
    let isFirst = true;
    let prevMessage = null;
    for (let message of messages) {
      message.groupPrev = false;
      message.groupNext = false;
      if (
        message.messageTimeMS &&
        message.messageType === 'text' &&
        !message.status !== 'hide'
      ) {
        if (
          !isFirst &&
          !message.timespan &&
          message.sender &&
          prevMessage.sender &&
          message.sender._id === prevMessage.sender._id &&
          message.messageTimeMS - prevMessage.messageTimeMS < 5 * 6000
        ) {
          prevMessage.groupNext = true;
          message.groupPrev = true;
        }
        isFirst = false;
        prevMessage = message;
      } else if (message.status !== 'hide') {
        isFirst = true;
      }
    }
  }

  addMembersMessage(message) {
    return i18n.t('{name} invites {members} to join.', {
      name: message.sender.nickname || i18n.t('Unknown Name'),
      members: JSON.parse(message.message)
        .map(member => {
          return member.nickname || i18n.t('Unknown Name');
        })
        .join(', ')
    });
  }
  // 縮成小訊息
  messageFormat(message, showSender = true) {
    let getSenderName = () => {
      if (!showSender || !message.sender) {
        return '';
      }
      if (message.sender._id === store.state.client.client) {
        return i18n.t('You');
      }
      return (message.sender.nickname || i18n.t('Unknown Name')) + ' ';
    };
    let getMemberName = () => {
      return message.member && message.member.nickname
        ? message.member.nickname
        : i18n.t('Unknown Name');
    };
    switch (message.messageType) {
      case 'url':
      case 'text':
        return message.message;
      case 'addMember':
      case 'joinRoom':
        return i18n.t('{name} joined room.', {
          name: getMemberName()
        });
      case 'addMembers':
        return this.addMembersMessage(message);
      case 'leaveRoom':
        return i18n.t('{name} leave room.', {
          name: getMemberName()
        });
      case 'deleteMember':
        return i18n.t('{sender} has removed {name}.', {
          sender: getSenderName(),
          name: getMemberName()
        });
      case 'sticker':
        return i18n.t('{name} send sticker.', {
          name: getSenderName()
        });
      case 'image':
        return i18n.t('{name} send image.', {
          name: getSenderName()
        });
      case 'video':
        return i18n.t('{name} send video.', {
          name: getSenderName()
        });
      case 'audio':
        return i18n.t('{name} send audio.', {
          name: getSenderName()
        });
      case 'file':
        return i18n.t('{name} send file.', {
          name: getSenderName()
        });
      case 'location':
        return i18n.t('{name} send location.', {
          name: getSenderName()
        });
      case 'recall':
        return i18n.t('{name} recall a message.', {
          name: getSenderName()
        });
      case 'encrypted':
        return i18n.t('{name} send an encrypted message.', {
          name: getSenderName()
        });
      case 'request_payment':
        return i18n.t('{name} send a payment request.', {
          name: getSenderName()
        });
      case 'payment':
        return i18n.t('{name} send a payment.', {
          name: getSenderName()
        });
      case 'template':
        return i18n.t('{name} send a template.', {
          name: getSenderName()
        });
      case 'flex':
        return i18n.t('{name} send a flex.', {
          name: getSenderName()
        });
      default:
        return i18n.t('Unsupported message.', {
          name: getSenderName()
        });
    }
  }
}

export default Message;



