import { mapActions } from 'vuex';
import VueTagsInput from '@johmun/vue-tags-input';
import Modal from '@/components/Components/Modal/index.vue';
import SetColor from './SetColor/index.vue';
import Tag from './Tag/index.vue';

export default {
  name: 'SetTags',
  props: ['defaultSetTags', 'defaultSetTagColors'],
  data() {
    return {
      tag: '',
      tags: [],
      color: '',
      colors: {},
      ogTag: '',
    };
  },
  components: {
    VueTagsInput,
    Modal,
    SetColor,
    Tag
  },
  computed: {
    autocompleteItems() {
      let items = [];
      this.$store.state.roomList.forEach(room => {
        if (room.pref && room.pref.tags) {
          items = items.concat(
            room.pref.tags.map(tag => ({ text: tag }))
          );
        }
      });

      return items;
    },
    filteredItems() {
      return this.autocompleteItems.filter(i => {
        return i.text.toLowerCase().indexOf(this.tag.toLowerCase()) !== -1;
      });
    }
  },
  mounted() {
    const { defaultSetTags, defaultSetTagColors } = this
    if (defaultSetTags) {
      this.tags = defaultSetTags;
      this.colors = defaultSetTags.reduce((prev, curr) => ({
        ...prev,
        [curr]: defaultSetTagColors[curr] || '#02B13F'
      }), {});
    }
  },
  methods: {
    async save() {
      if (this.tag) {
        this.update([{ text: this.tag }]);
      }
      this.$emit('save', {
        tags: this.tags,
        tagColors: this.colors,
      });
      this.cancel();
    },
    cancel() {
      this.ogTag = '';
      this.$emit('close');
    },
    update([newTag]) {
      const colorRegex = /^#[0-9a-f]{6}$/i;
      const { text } = newTag;
      let existIndex = this.tags.findIndex(tag => tag.toLowerCase() === text.toLowerCase());
      if (this.ogTag) {
        existIndex = this.tags.findIndex(tag => tag.toLowerCase() === this.ogTag.toLowerCase());
        this.ogTag = '';
      }
      if (existIndex < 0) {
        existIndex = this.tags.length;
      }
      this.tags[existIndex] = text;
      this.colors[text] = colorRegex.test(this.color) ? this.color : '#02B13F'
      this.color = '';
    },
    remove(tagText) {
      const { [tagText]: delKey, ...rest } = this.colors;
      this.tags = this.tags.filter(text => text !== tagText);
      this.colors = rest;
      this.ogTag = '';
    },
    select(tagText) {
      this.ogTag = tagText;
      this.tag = tagText;
      this.color = this.colors[tagText];
    },
    ...mapActions([])
  }
};



