import { mapGetters } from 'vuex';
import Swiper from 'swiper/dist/js/swiper.js';
import FileLoader from '@/components/Components/FileLoader/index.vue';
import localForage from 'localforage';
import lib from '@/lib';

export default {
  name: 'RoomChatOutputSilder',
  props: ['default', 'messages'],
  data() {
    return {
      swiperMain: null,
      swiperThumbs: null,
      tools: {
        hide: true
      },
      medias: [],
      focusIdx: null,
      downloadLink: null,
      downloadLinkTimer: null,
      zoomPosition: [],
      fixPosition: [0, 0],
      mouseDownPosition: []
    };
  },
  components: {
    FileLoader
  },
  computed: {
    focusMedia() {
      return this.medias[this.focusIdx];
    },
    fileName() {
      if (this.focusMedia.extra) {
        let extra = JSON.parse(this.focusMedia.extra);
        return `${this.focusMedia.message || this.focusMedia.id}${
          extra.fileExtension ? '.' + extra.fileExtension : ''
        }`;
      }
      return this.focusMedia.id;
    },
    zoomCssStyle() {
      const { zoomPosition, fixPosition } = this
      const transformOrigin = [
        zoomPosition[0] + fixPosition[0],
        zoomPosition[1] + fixPosition[1]
      ]
      return zoomPosition.length ? {
        transform: 'scale(2)',
        transition: '0.9s',
        transformOrigin: transformOrigin.map(pos => `${pos}px`).join(' ')
      } : { transition: '0.9s' }
    },
    swiperZoomContainerClass() {
      return this.zoomPosition.length ? 'swiper-zoom-container--zoom' : ''
    },
    ...mapGetters(['roomChat'])
  },
  mounted() {
    window.addEventListener('keyup', this.keyup);
    let messages = [];
    if (this.messages) {
      messages = this.messages;
    } else if (this.roomChat) {
      messages = this.roomChat.messages;
    }
    this.medias = messages.filter(_message => {
      return (
        (~['image', 'video'].indexOf(_message.messageType) && !_message.status) ||
        (_message.messageType === 'text' && _message.originalUrl && !_message.status)
      );
    }).flatMap(message => {
      if (message.messageType === 'image' && message.images && message.images.length > 0) {
        return message.images.map(image => {
          return { ...message, ...image, id: `${message.id}${image.originalUrl}` };
        });
      } else {
        return [message];
      }
    });
    if (messages.length === 0) {
      this.medias = [];
      this.close();
    }
    if (this.default) {
      this.focusIdx =
        this.medias.findIndex(media => {
          return media.id === this.default;
        }) || 0;
    }

    this.$nextTick(() => {
      // this.swiperInit();
    });

    // setTimeout(() => {
    //   this.tools.hide = true;
    // }, 1500);
  },
  methods: {
    keyup(e) {
      // ESC key
      if (e.keyCode === 27) {
        this.close();
      }
    },
    handleZoomStart(e) {
      if (e.target.nodeName === 'IMG') {
        const { clientX, clientY } = e;
        if (this.zoomPosition.length) {
          this.mouseDownPosition = [];
          this.zoomPosition = [];
        } else {
          this.zoomPosition = [clientX, clientY];
        }
      } else {
        this.zoomPosition = [];
      }
    },
    swiperInit() {
      if (!this.swiperMain) {
        this.swiperMain = new Swiper('.sliderMain .swiper-container', {
          sensitivity: 200,
          navigation: {
            nextEl: '.swiper-next',
            prevEl: '.swiper-prev'
          },
          // zoom: {
          //   maxRatio: 5
          // },
          on: {
            slideChange: () => {
              this.focusIdx = this.swiperMain.activeIndex;
              let videos = this.$refs.sliderMain.getElementsByTagName('video');
              for (let video of videos) {
                video.pause();
              }
              this.getDownloadLink();
            },
            // zoomChange: scale => {
            //   if (scale > 1) {
            //     this.tools.hide = true;
            //   }
            // }
          }
        });
        this.swiperThumbs = new Swiper('.sliderThumbs .swiper-container', {
          spaceBetween: 10,
          centeredSlides: true,
          slidesPerView: 'auto',
          touchRatio: 0.2,
          slideToClickedSlide: false
        });
        this.swiperMain.controller.control = this.swiperThumbs;
        this.swiperThumbs.controller.control = this.swiperMain;
      }
      this.swiperMain.slideTo(this.focusIdx, 1);
    },
    close() {
      this.$emit('close');
    },
    toggleToolsHide() {
      // if (this.swiperMain.zoom.scale > 1) {
      //   this.tools.hide = true;
      // } else {
      //   this.tools.hide = !this.tools.hide;
      // }
    },
    async getDownloadLink() {
      if (this.downloadLinkTimer) {
        clearTimeout(this.downloadLinkTimer);
        this.downloadLinkTimer = null;
      }
      let fileId = this.focusMedia.originalUrl.split('/').pop();
      let data = await localForage.getItem(`IMKit-${fileId}`);
      if (data) {
        let blob = lib.upload.b64ToBlob(data);
        this.downloadLink = URL.createObjectURL(blob);
      } else {
        this.downloadLink = null;
        this.downloadLinkTimer = setTimeout(this.getDownloadLink, 1000);
      }
    },
    handleClose (event) {
      const { className } = event.target;
      // window.removeEventListener('mousedown', this.handleImageMouseDown)
      ['fileLoaderMain', 'fileLoaderImage'].includes(className) && this.close() && (this.zoomPosition = [])
    },
    imageStyle (media) {
      return media.width > media.height ? 'horizontal' : 'vertical';
    },
    handleImageMouseDown (e) {
      if (e.target.nodeName === 'IMG' && this.zoomPosition.length) {
        this.mouseDownPosition = [e.offsetX, e.offsetY]
      }
    },
    handleImageMouseMove (e) {
      if (this.mouseDownPosition.length) {
        const fixX = this.mouseDownPosition[0] - e.offsetX;
        const fixY = this.mouseDownPosition[1] - e.offsetY;
        this.fixPosition = [fixX * 2, fixY * 2];
      }
    },
    handleImageMouseUp (e) {
      if (e.target.nodeName === 'IMG' && this.zoomPosition.length) {
        this.zoomPosition = [
          this.zoomPosition[0] + this.fixPosition[0],
          this.zoomPosition[1] + this.fixPosition[1]
        ];
        this.mouseDownPosition = [];
        this.fixPosition = [0, 0];
      }
    },
  },
  beforeDestroy() {
    window.removeEventListener('keyup', this.keyup);
  }
};



